import { authHeader } from '../_helpers';
import axios from 'axios'

export const usuarioEstabelecimentoService = {
    getAll,
    usuarioCreate,
    usuarioRemove,
    usuarioUpdate,
    imagemUpload
};

function imagemUpload(id, photo) {

    let formData = new FormData()
    formData.append('photo', photo, photo.name);
    const requestOptions = {

        headers: authHeader(true)
    };
    return axios.post(`/usuarios/upload/${id}`, formData, requestOptions).then(data => {
        return data.data
    })
}

function getAll() {
    let estabelecimento = JSON.parse(localStorage.getItem('estabelecimento'))
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };

    return axios.get(`/usuarios/all/${estabelecimento._id}`, requestOptions)
        .then(users => {
            return users.data
        })
}
/**
 * 
 * @param {User} user 
 */
function usuarioCreate(user) {
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };
    return axios.post('/usuarios', {
        usuario: user
    }, requestOptions).then(data => {
        return data.data
    })
}
/**
 * 
 * @param {User} user 
 */
function usuarioUpdate(user) {
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };
    return axios.post('/usuarios/alterar', {
        usuario: user
    }, requestOptions).then(data => {
        return data.data
    })
}
/**
 * 
 * @param {id} id 
 */
function usuarioRemove(id) {
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };
    return axios.delete(`/usuarios/${id}`, requestOptions).then(data => {
        return data.data
    })
}



